	module.exports = {"data":{"markdownRemark":{"html":"<p>基本、公私ともにプログラマです。ゲーム制作に関わること全般……お絵かきやシナリオ制作、Webページ制作、作曲など、プログラミング程得意ではないですがある程度はできます。ゲーム制作関係でやったことないのは声優くらいだろうか。(猫の声真似程度しかできない)あとWebゲーム・Webアプリなんかもちょこっと作れます。一応LAMPの知識も本当に最低限ですがあります。この辺が趣味であり、特技でもあるかなぁ。 アウトドア関係では水泳(泳ぐというより、プカプカ浮かぶだけ:p)やスーパー銭湯巡り。ここ数年資金的な関係で凍結していますが二輪のカスタマイズやショートツーリングなんかも好きでよくやっていました。あと興味があるのがカヤックでの川下り。ラフティング体験ツアーで6人乗りのダッキーに乗ったことはあるのですが、一人でもやってみたいなぁ、と企んでいます。まぁ資金的な関係で多分あと4～5年はやらないでしょうけどね。 なんか趣味がオヤジ化してきたような気がするが、あと10～20年は若者のカテゴリに居たいなぁ 'Ａ｀</p>","frontmatter":{"cover":null,"date":"2012-11-27T22:40:41+09:00","strDate":"2012/11/27","title":"代表の趣味や特技"}}},"pathContext":{"path":"/ja/blog/2012/11/27-代表の趣味や特技/","langKey":"ja"}}

